#hero{
    height: 100vh;
    width: 100%;
    background:linear-gradient(0deg, rgba(31, 31, 31, 0.8), rgba(31, 31, 31, 0.8)), url('/herobg.jpg');
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
#hero #overlay{
    height: 100%;
    width: 100%;
    position: absolute;
    background-color: black;
    opacity: .9;
    z-index: 1;
}
#hero #main{
    height: 100vh;
    width: 100%;
    z-index: 100;
    color: white;
    padding: 7%;
}
#hero #btn-learn{
    width: 70%;
    height: 50px;
    border-radius: 3px;
    border:none;
    color:white;
    background-color: #034078;
}
#hero #desc-container{
    border-radius: 3px;
    border: 2px solid #034078;
    padding: 10px;
    font-size: .9rem;
}